			// éªŒè¯ç™»å½•çŠ¶æ€ï¼ˆç”¨äºè°ƒç”¨äº‘å‡½æ•°å‰ï¼‰
			async validateLoginBeforeSave() {
				const token = uni.getStorageSync('uni_id_token')
				if (!token) {
					uni.showModal({
						title: 'éœ€è¦ç™»å½•',
						content: 'ä¿å­˜å„¿ç«¥ä¿¡æ¯éœ€è¦å…ˆç™»å½•ï¼Œæ˜¯å¦å‰å¾€ç™»å½•ï¼Ÿ',
						confirmText: 'å»ç™»å½•',
